import React, { useEffect } from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import ReactLoading from 'react-loading';
import { gsap } from 'gsap';

const FiszkiWrapper = styled.div`
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: #23283d;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(1, 1fr);
  position: relative;
  opacity: 0;

  @media screen and (orientation: portrait) {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
`;

const Buttons = styled.div`
  height: 70vh;
  width: 40vw;
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(1, 1fr);
  align-items: center;
  justify-items: center;
  margin-top: 15vh;

  @media screen and (orientation: portrait) {
    width: 90vw;
    margin-left: 5vw;
    margin-top: 1vh;
    height: 40vh;
  }

  button {
    width: 18vw;
    height: 12vh;
    font-size: 2vw;
    border: none;
    border-radius: 5vw;
    cursor: pointer;
    outline: none;
    @import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');
    font-family: 'Lato', sans-serif;
    background-color: #303754;
    color: white;
    transition: background-color 500ms;
    will-change: background-color;
    :hover {
      background-color: #303779;
    }

    @media screen and (orientation: portrait) {
      width: 35vh;
      height: 10vh;
      border-radius: 7vh;
      font-size: 3.5vh;
    }
  }
`;

const FButton = styled.button`
  width: 18vw;
  height: 12vh;
  font-size: 2vw;
  border: none;
  border-radius: 5vw;
  cursor: pointer;
  outline: none;
  @import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');
  font-family: 'Lato', sans-serif;
  background-color: #303754;
  color: white;
  transition: background-color 500ms;
  will-change: background-color;
  :hover {
    background-color: #303779;
  }

  @media screen and (orientation: portrait) {
    width: 35vh;
    height: 10vh;
    border-radius: 7vh;
    font-size: 3.5vh;
  }
`;

const Card = styled.div`
  width: 55vw;
  height: 70vh;
  display: block;
  margin-top: 15vh;
  margin-right: 5vw;

  @media screen and (orientation: portrait) {
    margin-right: 0;
    border-radius: 5vh;
    width: 90vw;
    height: 40vh;
    margin-left: 5vw;
  }
`;

const FrontCard = styled.div`
  width: 55vw;
  height: 70vh;
  background-color: #303754;
  position: absolute;
  border-radius: 3vw;
  transform: ${(props) =>
    props.flip === true
      ? 'perspective(900px) rotateX(-180deg)'
      : 'perspective(900px) rotateX(0deg)'};
  backface-visibility: hidden;
  transition: 600ms;

  h1 {
    display: block;
    color: white;
    font-size: 4vw;
    margin: 0;
    width: 25vw;
    padding-left: 15vw;
    text-align: center;
    height: 15vh;
    padding-top: 25vh;
  }

  @media screen and (orientation: portrait) {
    margin-right: 0;
    border-radius: 5vh;
    width: 90vw;
    height: 40vh;

    h1 {
      font-size: 5vh;
      width: 90vw;
      padding-left: 0;
      height: 10vh;
      padding-top: 17.5vh;
    }
  }
`;

const BackCard = styled.div`
  width: 55vw;
  height: 70vh;
  background-color: #303759;
  position: absolute;
  border-radius: 3vw;
  transform: ${(props) =>
    props.flip === true
      ? 'perspective(900px) rotateX(0deg)'
      : 'perspective(900px) rotateX(180deg)'};
  backface-visibility: hidden;
  transition: 600ms;

  h1 {
    display: block;
    color: white;
    font-size: 4vw;
    margin: 0;
    width: 25vw;
    padding-left: 15vw;
    text-align: center;
    height: 15vh;
    padding-top: 25vh;
  }

  @media screen and (orientation: portrait) {
    margin-right: 0;
    border-radius: 5vh;
    width: 90vw;
    height: 40vh;

    h1 {
      font-size: 5vh;
      width: 90vw;
      padding-left: 0;
      height: 10vh;
      padding-top: 17.5vh;
    }
  }
`;

const LoadingWrapper = styled.div`
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  opacity: 0;

  animation: appear 1.1s ease-in;

  @keyframes appear {
    0% {
      opacity: 0;
    }
    30% {
      opacity: 1;
    }
    80% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
`;

const Fiszki = ({ data }) => {
  const [array, setArray] = React.useState(data);
  const [counter, setCounter] = React.useState(0);
  const [orientation, setOrientation] = React.useState('Landscape');
  const [flip, setFlip] = React.useState(false);
  const [loading, setLoading] = React.useState(true);
  const myRef = React.useRef(null);
  const secondRef = React.useRef(null);

  useEffect(() => {
    const tl = gsap.timeline();
    const wrapper = myRef.current;
    const second = secondRef.current;

    tl.set(second, { opacity: 1 });

    tl.set(wrapper, { opacity: 0 }).to(wrapper, 0.6, { opacity: 1 });
  }, [loading]);

  useEffect(() => {
    setTimeout(() => setLoading(false), 1100);
  }, []);

  useEffect(() => {
    setArray(data.filter((item) => item.id === counter));
  }, [counter]);

  useEffect(() => {
    setInterval(() => {
      if (window.innerWidth > window.innerHeight) {
        setOrientation('Landscape');
      } else if (window.innerWidth <= window.innerHeight) {
        setOrientation('Portrait');
      }
    }, 10);
  });

  const flipCard = async () => {
    let flipped = flip;
    await setFlip(!flipped);
  };

  const add = () => {
    let arr = [];
    arr = [...data];
    if (counter < arr.length - 1) {
      let numb = counter;
      numb++;
      setCounter(numb);
    }
    setFlip(false);
  };

  const reduce = () => {
    if (counter > 0) {
      let numb = counter;
      numb--;
      setCounter(numb);
    }
    setFlip(false);
  };

  return (
    <>
      {loading === true ? (
        <FiszkiWrapper ref={secondRef}>
          <LoadingWrapper>
            <ReactLoading type={'spin'} />
          </LoadingWrapper>
        </FiszkiWrapper>
      ) : (
        <FiszkiWrapper ref={myRef}>
          {orientation === 'Landscape' && (
            <>
              <Buttons>
                <FButton onClick={flipCard}>Obróć</FButton>
                <FButton onClick={add}>Następna</FButton>
                <FButton onClick={reduce}>Poprzednia</FButton>
              </Buttons>
              <Card>
                <FrontCard flip={flip}>
                  <h1>{ array[0].question }</h1>
                </FrontCard>
                <BackCard flip={flip}>
                  <h1>{ array[0].answer }</h1>
                </BackCard>
              </Card>
            </>
          )}
          {orientation === 'Portrait' && (
            <>
              <Card>
                <FrontCard flip={flip}>
                  <h1>{ array[0].question }</h1>
                </FrontCard>
                <BackCard flip={flip}>
                  <h1>{ array[0].answer }</h1>
                </BackCard>
              </Card>
              <Buttons>
                <FButton onClick={flipCard}>Obróć</FButton>
                <FButton onClick={add}>Następna</FButton>
                <FButton onClick={reduce}>Poprzednia</FButton>
              </Buttons>
            </>
          )}
        </FiszkiWrapper>
      )}
    </>
  );
};

const mapStateToProps = (state) => {
  return { data: state };
};

export default connect(mapStateToProps)(Fiszki);
