import React from 'react';
import styled from 'styled-components';
import reading from '../images/reading.png';
import { gsap } from 'gsap';
import { useHistory } from 'react-router-dom';

const Wrapper = styled.div`
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: #23283d;
`;

const UpperDiv = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(1, 1fr);
  justify-items: center;
  margin-top: 8vh;
  align-items: center;

  @media screen and (orientation: portrait) {
    width: 100vw;
    margin-left: 0;
  }

  h1 {
    @import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');
    font-family: 'Lato', sans-serif;
    font-size: 5vw;
    width: 40vw;
    display: block;
    color: white;
    margin: 0;
    opacity: 0;

    @media screen and (orientation: portrait) {
      font-size: 6.2vh;
    }
  }
  h1::after {
    content: '';
    display: block;
    width: 100%;
    height: 0.5vh;
    background-color: white;
    animation: appear 1s ease-in-out;
    animation-delay: 0.6s;

    @media screen and (orientation: portrait) {
    }
  }

  @keyframes appear {
    from {
      width: 0%;
    }
    to {
      width: 100%;
    }
  }
`;

const Logo = styled.div`
  width: 20vw;
  height: 20vw;
  display: block;
  background-image: url(${reading});
  background-size: cover;
  opacity: 0;

  @media screen and (orientation: portrait) {
    width: 20vh;
    height: 20vh;
  }
`;

const DownDiv = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(1, 1fr);
  width: 100vw;
  align-items: center;
  justify-items: center;
  margin-top: 15vh;

  @media screen and (orientation: portrait) {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(2, 1fr);
    height: 60vh;
    margin-top: 10vh;
  }
`;

const OptionButton = styled.button`
  width: 27vw;
  height: 18vh;
  font-size: 3vw;
  border: none;
  border-radius: 5vw;
  cursor: pointer;
  outline: none;
  opacity: 0;
  @import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');
  font-family: 'Lato', sans-serif;
  background-color: #303754;
  color: white;
  transition: background-color 500ms;
  will-change: background-color;
  :hover {
    background-color: #303779;
  }

  @media screen and (orientation: portrait) {
    width: 40vh;
    height: 15vh;
    border-radius: 7vh;
    font-size: 4.5vh;
  }
`;

const HomeTitle = styled.h1`
  @import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');
  font-family: 'Lato', sans-serif;
  font-size: 5vw;
  width: 40vw;
  display: block;
  color: white;
  margin: 0;
  opacity: 0;

  @media screen and (orientation: portrait) {
    font-size: 6.2vh;
  }

  ::after {
    content: '';
    display: block;
    width: 100%;
    height: 0.5vh;
    background-color: white;
    animation: appear 1s ease-in-out;
    animation-delay: 0.6s;
  }
`;

const Home = () => {
  const history = useHistory();
  const upRef = React.useRef(null);
  const downRef = React.useRef(null);
  React.useEffect(() => {
    const tl = gsap.timeline();
    const logo = upRef.current.children[0];
    const h1 = upRef.current.children[1];
    const opt1 = downRef.current.children[0];
    const opt2 = downRef.current.children[1];

    tl.set([logo, h1, opt1, opt2], { autoAlpha: 0 })
      .to(logo, 0.6, { autoAlpha: 1 })
      .to(h1, 0.6, { autoAlpha: 1 })
      .to([opt1, opt2], 1, { autoAlpha: 1 });
  });
  return (
    <Wrapper>
      <UpperDiv ref={upRef}>
        <Logo />
        <HomeTitle>Phrasal Verbs</HomeTitle>
      </UpperDiv>
      <DownDiv ref={downRef}>
        <OptionButton
          onClick={() => {
            history.push('/phrasalverbs/fiszki/');
          }}
        >
          Fiszki
        </OptionButton>
        <OptionButton
          onClick={() => {
            history.push('/phrasalverbs/learn/');
          }}
        >
          Naucz się
        </OptionButton>
      </DownDiv>
    </Wrapper>
  );
};

export default Home;
