import React from 'react';
import styled, { css } from 'styled-components';
import { connect } from 'react-redux';
import ReactLoading from 'react-loading';
import { gsap } from 'gsap';

const LearnWrapper = styled.div`
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: repeat(2, 1fr);
  align-items: center;
  justify-items: center;
  background-color: #23283d;
  opacity: 0;
`;

const QuestionWrapper = styled.div`
  width: 100vw;
  height: 50vh;
  display: flex;
  justify-content: center;
`;

const AnswersWrapper = styled.div`
  width: 100vw;
  height: 50vh;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  justify-items: center;
  align-items: center;

  @media screen and (orientation: portrait) {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(4, 1fr);
  }
`;

const QuestionCard = styled.div`
  width: 45vw;
  height: 45vh;
  margin-top: 2.5vh;
  background-color: #303754;
  position: absolute;
  border-radius: 3vw;
  display: flex;
  align-items: center;
  justify-content: center;

  h1 {
    display: block;
    color: white;
    font-size: 5vw;
    margin: 0;
    text-align: center;
    height: 15vh;
  }

  @media screen and (orientation: portrait) {
    width: 90vw;
    border-radius: 6vh;

    h1 {
      font-size: 7vh;
    }
  }
`;

const AnswerButton = styled.button`
  width: 36vw;
  height: 18vh;
  font-size: 2vw;
  border: none;
  border-radius: 5vw;
  cursor: pointer;
  outline: none;
  @import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');
  font-family: 'Lato', sans-serif;
  background-color: #303754;
  color: white;
  transition: background-color 500ms;
  will-change: background-color;

  ${(props) =>
    props.ans === 'wrong' &&
    css`
      background-color: red;
    `}

  ${(props) =>
    props.ans === 'good' &&
    css`
      background-color: green;
    `}

  @media screen and (orientation: portrait) {
    width: 90vw;
    height: 10vh;
    border-radius: 7vh;
    font-size: 3.5vh;
  }
`;

const LoadingWrapper = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

  opacity: 0;

  animation: appear 1.1s ease-in;

  @keyframes appear {
    0% {
      opacity: 0;
    }
    30% {
      opacity: 1;
    }
    80% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
`;

const Learn = ({ data }) => {
  const [arr, setArr] = React.useState(data);
  const [loading, setLoading] = React.useState(true);
  const [question, setQuestion] = React.useState({
    q1: arr[0].question,
    a1: arr[0].answer,
    a2: arr[1].answer,
    a3: arr[2].answer,
    a4: arr[3].answer,
  });
  const [colors, setColors] = React.useState(['', '', '', '']);
  const firstRef = React.useRef(null);
  const secondRef = React.useRef(null);

  React.useEffect(() => {
    const tl = gsap.timeline();
    const first = firstRef.current;
    const second = secondRef.current;

    tl.set(second, { opacity: 1 });
    tl.set(first, { opacity: 0 }).to(first, 0.6, { opacity: 1 });
  }, [loading]);

  React.useEffect(() => {
    setTimeout(() => setLoading(false), 1100);
  }, []);

  const randomQuestion = () => {
    setColors(['', '', '', '']);
    const min = 0;
    const max = data.lenght - 1;
    const questionID = Math.floor(Math.random() * 78);

    let goodAnswer = Math.floor(Math.random() * (3 - 0 + 1)) + 0;

    let newArr = [];

    newArr = arr.filter((item) => item.answer !== arr[questionID].answer);

    switch (goodAnswer) {
      case 0:
        setQuestion({
          q1: arr[questionID].question,
          a1: arr[questionID].answer,
          a2: newArr[Math.floor(Math.random() * 46)].answer,
          a3: newArr[Math.floor(Math.random() * 46)].answer,
          a4: newArr[Math.floor(Math.random() * 46)].answer,
        });
        break;
      case 1:
        setQuestion({
          q1: arr[questionID].question,
          a1: arr[Math.floor(Math.random() * 46)].answer,
          a2: arr[questionID].answer,
          a3: arr[Math.floor(Math.random() * 46)].answer,
          a4: arr[Math.floor(Math.random() * 46)].answer,
        });
        break;
      case 2:
        setQuestion({
          q1: arr[questionID].question,
          a1: newArr[Math.floor(Math.random() * 46)].answer,
          a2: newArr[Math.floor(Math.random() * 46)].answer,
          a3: arr[questionID].answer,
          a4: newArr[Math.floor(Math.random() * 46)].answer,
        });
        break;
      case 3:
        setQuestion({
          q1: data[questionID].question,
          a1: newArr[Math.floor(Math.random() * 46)].answer,
          a2: newArr[Math.floor(Math.random() * 46)].answer,
          a3: newArr[Math.floor(Math.random() * 46)].answer,
          a4: data[questionID].answer,
        });
        break;
      default:
        break;
    }
  };

  const checkAnswer = (e) => {
    const answer = e.target.id;

    const quest = arr.filter((item) => item.question === question.q1);
    console.log(quest);

    if (answer === quest[0].answer) {
      switch (e.target.value) {
        case '0':
          setColors(['good', '', '', '']);
          break;
        case '1':
          setColors(['', 'good', '', '']);
          break;
        case '2':
          setColors(['', '', 'good', '']);
          break;
        case '3':
          setColors(['', '', '', 'good']);
          break;
      }
    } else {
      switch (e.target.value) {
        case '0':
          setColors(['wrong', '', '', '']);
          break;
        case '1':
          setColors(['', 'wrong', '', '']);
          break;
        case '2':
          setColors(['', '', 'wrong', '']);
          break;
        case '3':
          setColors(['', '', '', 'wrong']);
          break;
      }
    }

    setTimeout(randomQuestion, 1000);
  };
  return (
    <>
      {loading === true ? (
        <LearnWrapper ref={secondRef}>
          <LoadingWrapper>
            <ReactLoading type={'spin'} />
          </LoadingWrapper>
        </LearnWrapper>
      ) : (
        <LearnWrapper ref={firstRef}>
          <QuestionWrapper>
            <QuestionCard>
              <h1>{question.q1}</h1>
            </QuestionCard>
          </QuestionWrapper>
          <AnswersWrapper>
            <AnswerButton
              value={0}
              ans={colors[0]}
              onClick={checkAnswer}
              id={question.a1}
            >
              {question.a1}
            </AnswerButton>
            <AnswerButton
              value={1}
              ans={colors[1]}
              onClick={checkAnswer}
              id={question.a2}
            >
              {question.a2}
            </AnswerButton>
            <AnswerButton
              value={2}
              ans={colors[2]}
              onClick={checkAnswer}
              id={question.a3}
            >
              {question.a3}
            </AnswerButton>
            <AnswerButton
              value={3}
              ans={colors[3]}
              onClick={checkAnswer}
              id={question.a4}
            >
              {question.a4}
            </AnswerButton>
          </AnswersWrapper>
        </LearnWrapper>
      )}
    </>
  );
};

const mapStateToProps = (state) => {
  return { data: state };
};

export default connect(mapStateToProps)(Learn);
